import { DatetimePicker,Popup  } from 'vant';
import Vue from "vue";
Vue.use(Popup)
Vue.use(DatetimePicker);
export default {
  data(){
    return {
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      show: true
    }
  },
  components: {
    DatetimePicker
  }
}



